export const initWelcome = (params) => {
  const div = document.createElement("div");
  div.innerHTML = `
    <text-style tag="h1">Piedra Papel <span>√≥</span> Tijera</text-style>
    <my-button>Empezar</my-button>
    <my-play></my-play>
    `;

  const button = div.querySelector("my-button") as Element;

  button.addEventListener("click", () => {
    params.goTo("/instruccion");
  });
  return div;
};
